<template lang="jade">
  .help-page
    slot(name="cover")
    slot(name="movebar")
    slot(name="resize-x")
    slot(name="resize-y")
    slot(name="toolbar")
    .scroll-content.function-help
      .ds-button-group
        .ds-button.text-button(:class="{ selected: index === i }" @click=" index = i " v-for=" (g, i) in games ") {{ g.tagName }}
      .content(v-html=" games[index].content ")

    
</template>

<script>
  import api from '../../http/api'
  export default {
    data () {
      return {
        games: [{}],
        index: 0
      }
    },
    mounted () {
      this.getHelpInfo()
    },
    methods: {
      getHelpInfo () {
        this.$http.get(api.getHelpInfo, {subject: 'comment'}).then(({data}) => {
          if (data.success === 1) {
            this.games = data.items
          } else this.$message.warning({target: this.$el, message: '功能帮助信息获取失败！'})
        }, (rep) => {
          this.$message.warning({target: this.$el, message: '功能帮助信息获取失败！'})
        })
      }
    }
  }
</script>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  .function-help
    top TH
    text-align center
    padding PWX
    .ds-button-group
      text-align left
      height auto
    .content
      text-align left
</style>


