<template lang="jade">
  .login-test
    h2 线路检测
    el-row.routers.font-white(:gutter="30" style="width: 9.0003rem; padding-left: 0; padding-right: 0")
      el-col(:span="8" v-for=" (r, index) in routers "  @click.native="goLogin")
        .col-content(v-bind:class="{ fast:  fast === r.value, usual: r.usual}")
          span.route-index {{ index + 1 }}
          |  线
          SignalBar(:value=" r.value || 6 ")
          .timer 
            span.time {{ r.time || 10 }}
            |  秒

    
</template>

<script>
  import SignalBar from 'components/SignalBar'
  import api from '../../http/api'
  export default {
    components: {
      SignalBar
    },
    data () {
      return {
        routers: []
      }
    },
    computed: {
      fast () {
        let v = 0
        this.routers.forEach(r => {
          v < r.value && (v = r.value)
        })
        return v
      }
    },
    mounted () {
      this.routers = [
        {value: 5, time: 50, usual: true},
        {value: 10, time: 0},
        {value: 3, time: 70},
        {value: 1, time: 120},
        {value: 8, time: 20}
      ]
    },
    methods: {
      getHelpInfo () {
        this.$http.get(api.getHelpInfo, {subject: 'faq'}).then(({data}) => {
          if (data.success === 1) {
          } else this.$message.warning({target: this.$el, message: '线路信息获取失败！'})
        }, (rep) => {
          this.$message.warning({target: this.$el, message: '线路信息获取失败！'})
        })
      },
      goLogin () {
        this.$router.push('/login/login')
      }
    }
  }
</script>

<style lang="stylus">

  .login-test + .download
    display none
    
</style>

<style lang="stylus" scoped>
  @import '../../var.stylus'
  W = 2.7rem
  H = 1.8rem
  .route-index
  .time
    font-family Roboto
    font-size 0.72rem
    vertical-align sub
    text-shadow()
  .time
    font-size .3rem
  .login-test
    text-align center
    color #fff
    h2
      font-size .18rem
      font-weight normal

      
  .routers
    padding .5rem 1rem
    text-align center
    display inline-block
    .col-content
      position relative
      height H
      padding 0 PWX
      margin-bottom .3rem
      radius()
      text-align left
      overflow hidden
      background-color rgba(255, 255, 255, .2)
      box-shadow .02rem .02rem .02rem rgba(0, 0, 0, .2)
      cursor pointer
      &:hover
        background-color rgba(255, 255, 255, .4)
      &:active
        background-color rgba(255, 255, 255, .6)
      &:after
        background-color GOLD
        position absolute
        right 0
        top 0
        width 2rem
        text-align center
        padding .05rem 0
        font-shadow()
        transform rotateZ(40deg) translateY(-100%)  translateX(30%) 
      &.fast:after
        content '最快'
        bg-gradient(180deg, #ff7200, #ff5e00)
      &.usual:after
        content '常用'
        bg-gradient(180deg, #1ab8f3, #1a9ff3)
        
        
      
      .timer
        text-align right
      

</style>


